    grid-gap:50px;
    gap:50px;
}
.section{
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap:wrap;
    font-family: "Poppins", sans-serif;
    margin:0 auto;
    max-width:1200px;
    box-sizing:border-box;
    padding:20px;

}

.section.img-right{

    flex-direction:row-reverse;
}

.section__price{
    font-weight: 500;
    font-size: 20px;
    color: #B08EAD;
}

.section__note{
    font-size: 40px;
    line-height: 1.5;
    font-weight: 500;
    color: #4B3049;

}

.section__btn{
    display: inline-block;
    padding: 18px 36px;
    border-radius: 5px;
    background-color: transparent;
    border: 1px solid #B08EAD;
    font-size: 15px;
    font-weight: 700;
    color: #B08EAD;
    text-transform: uppercase;
    font-weight: 400;
    transition: 0.5s;
    max-width:180px;
}

.section__btn:hover{
    background-color:#B08EAD;
    color:white;
    
}

.section-col-img{
    flex:1 1 400px;
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    position:relative;
    max-width:-webkit-max-content;
    max-width:max-content;
}

.section-col-text{
    flex:1 1 400px;
    box-sizing:border-box;
    padding:20px;
    display:flex;
    flex-direction:column;
    grid-gap:20px;
    gap:20px;
}


.section-col-img img{
box-sizing:border-box;
padding:50px 0px 0px 50px;
width:100%;
}

.section-col-img::after{
content:"";
background-color: #B08EAD;
height:calc(100% - 50px);
width:calc(100% - 50px);
position:absolute;
top:0px;
left:0px;
z-index:-1;
}


.img-right .section-col-img::after{
    top:0px;
    left:50px;
}


.img-right .section-col-img img{
    padding:50px 50px 0px 0px;
}
