.Header{
    display:flex;
    justify-content: center;
    height:80px;
    position:relative;
    
}

.Header.active{
    width:100%;
    position:fixed;
    z-index:123;
    background-color: white;
    -webkit-animation-name: headeranimate;
            animation-name: headeranimate;
    -webkit-animation-duration: 1s;
            animation-duration: 1s; 
}

@-webkit-keyframes headeranimate {
    from {top:-80px; opacity:0.1}
    to {top:0px; opacity:1}
  }

@keyframes headeranimate {
    from {top:-80px; opacity:0.1}
    to {top:0px; opacity:1}
  }

.Header-content{
    box-sizing:border-box;
    padding:0px 20px;
    
}


.Header-content{
    flex:1 1;
    display:flex;
    justify-content:space-between;
    align-items: center;
    max-width:1200px;
    background-color:transparent ;
}

.nav-bar, .sideNav{
    height:100%;
    display:flex;
}



.nav-bar__items{
    flex:1 1;
    display:flex;
    justify-content: space-between;
    align-items: center;
    font-family: 'Rubik', sans-serif;
    text-transform: capitalize;
    grid-gap:0px 20px;
    gap:0px 20px;
    
}

.nav-bar__item, .nav-item__dropDown{

font-size:16px;
font-weight:400;
line-height:16px;

cursor: pointer;
height: 100%;
display:flex;
justify-content: center;
align-items: center;
position:relative;
}

.nav-item__header{
    display:flex;
    justify-content: space-between;
    grid-gap:0px 5px;
    gap:0px 5px;
    cursor: pointer;
}
.header-icons{
    display:flex;
    justify-content: space-between;
    align-items: center;
    grid-gap:20px;
    gap:20px;
    
}

.header-icons > *{
cursor: pointer;
height:20px;
width:20px;
}

.nav-item__options{
    display:none;
    border-radius: 5px;
    box-sizing:border-box;
    padding:15px;
    font-size: 14px;
    background-color: rgb(176, 142, 173);
    width:-webkit-max-content;
    width:max-content;
}

.nav-item__option{
    text-transform: capitalize;
    font-family: 'Rubik', sans-serif;
    font-weight:400;
    color:white;
    white-space: nowrap;
}

.nav-item__options.active{
    display: block;
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    grid-gap: 5px;
    gap: 5px;
    

    top: 80px;
    left:-15px;
    box-sizing:border-box;
    -webkit-animation-name: navAnimation;
            animation-name: navAnimation;
    -webkit-animation-duration: 0.5s;
            animation-duration: 0.5s; 
    -webkit-transform-origin: center top; 
            transform-origin: center top;
}

@-webkit-keyframes navAnimation {
    from {-webkit-transform: scaleY(0);transform: scaleY(0)}
    to {-webkit-transform: scaleY(1);transform: scaleY(1)}
  }

@keyframes navAnimation {
    from {-webkit-transform: scaleY(0);transform: scaleY(0)}
    to {-webkit-transform: scaleY(1);transform: scaleY(1)}
  }

.burger-icon{
    display:none;
    width:20px;
    height:20px;
}

.offcanvas-nav{
    width:230px;
}

@media(max-width:992px){
    .nav-bar{
        display:none;
    }
    .sideNav{
        display:block;
    }

    .nav-bar__items{
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        grid-gap: 20px;
        gap: 20px;
    }

    .nav-item__dropDown{
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        grid-gap: 20px;
        gap: 20px;
        
    }

    .nav-item__options.active{
        position:relative;
        top:0px;
        align-items: flex-start;
        
    }

    .burger-icon{
        display:block;
    }

    .logo{
        width:140px;
    }
    .logo img{
        width:100%;
    }
}  

